import React,{PureComponent} from 'react';
import USMapContainer from './USMapContainer';
import {connect} from 'react-redux';
import ReactTooltip from 'react-tooltip';
import {Box, Button, Container, Link, Typography} from "@material-ui/core";
import Copyright from "../component/Copyright";

class Main extends PureComponent{
  render(){
    const {tooltip,isLoading}=this.props;

    if(isLoading){
        return 'Loading';
    }

    return(
        <Container lg={3} height={'90vh'} width={'100%'}>
            <Box>
                <Typography variant="h3">US Map</Typography>
            </Box>
            <Box>
                <Button>Views</Button>
            </Box>
            <Box lg={3} height={'70vh'} width={'100%'}>
                <USMapContainer />
                <ReactTooltip>{tooltip}</ReactTooltip>
            </Box>
            <Box height={'auto'} lg={3}>
                <Typography variant="caption" gutterBottom>
                    Source code: <a href="https://github.com/harryphan/us-district-map">Github</a>
                    <br/>
                    Data sources: <a href="https://www.cnn.com/election/2020/results/president?iid=politics_election_national_map">CNN</a>
                    &nbsp;
                    <a href="https://www.politico.com/2020-election/results/">Politico</a>
                </Typography>
            </Box>
            <Box height={'auto'}>
                <Copyright />
            </Box>
        </Container>
    );
  }
}

const mapStateToProps = state =>{
  return {
    tooltip:state.mapParams.tooltip,
    isLoading:state.votingData.isLoading || state.votingData.isLoadingCounties,
  };
}



export default connect(mapStateToProps)(Main);
